import React from "react";
import {
  CRow,
  CCard,
  CCardBody,
  CCol,
  CCardHeader,
  CButton,
} from "@coreui/react";
import { useEffect, useState } from "react";
import DataTable from "react-data-table-component";
import axios from "axios";
import { Link } from "react-router-dom";
import styled from "styled-components";
import Swal from "sweetalert2";
import { FiEye } from "react-icons/fi";
import CustomeLoader from "../../../components/loader/CustomeLoader";
import { HiArrowSmLeft  } from "react-icons/hi";
// or

const User = () => {

  const [user, setUser] = useState([]);

  const [rows, setRows] = useState([]);
  const [pending, setPending] = useState(true);

  const token =
    typeof window !== "undefined" ? localStorage.getItem("status") : null;
  const [filterText, setFilterText] = useState("");
  const [resetPaginationToggle, setResetPaginationToggle] = useState(false);

  // --------------------------------------------------FETCH ALL User------------------------------------------


  useEffect(() => {
    const faqObj = new FormData();
    faqObj.append("user_type", "1");
    axios
      .post("alluserslist", faqObj, {
        headers: {
          Accept: "application/json",
          Authorization: "Bearer " + token,
        },
      })
      .then((res) => {
        res.data.status == 200 ?   setUser(res.data.data):  setUser([])
        // setUser(res.data.data);
        console.log("User", res.data.data);
      });
  }, [token]);

  // ---------------------------------------------------------------------------------------------------------------------
  const Toast = Swal.mixin({
    toast: true,
    position: "top-end",
    showConfirmButton: false,
    timer: 3000,
    timerProgressBar: true,
    didOpen: (toast) => {
      toast.addEventListener("mouseenter", Swal.stopTimer);
      toast.addEventListener("mouseleave", Swal.resumeTimer);
    },
  });
  // --------------------------------------------------DELETE User---------------------------------------------

  //   const deleteHandler = (row) => {
  //     axios
  //       .delete("" + row.id, {
  //         headers: {
  //           Accept: "application/json",
  //           Authorization: "Bearer " + token,
  //         },
  //       })
  //       .then((result) => {
  //         Toast.fire({
  //           icon: "success",
  //           title: result.data.message,
  //         });
  //         console.log(result);
  //         let newArr = user.filter((items) => {
  //           return row.id !== items.id;
  //         });
  //         setUser(newArr);
  //         console.log(newArr);
  //         // window.location.reload(false);
  //       })
  //       .catch((error) => {
  //         console.log(error);
  //       });
  //   };

  // --------------------------------------------------------------------------------------------------------------

  // ------------------------------------------------User TABLE COLUMN-----------------------------------------
  // const data = [
  //   {

  //       phoneno: "1234567890",
  //       username: "abc1234",
  //       name: "1234abc",
  //       city: "bhopal",
  //       gender: "male",

  //     },
  // ]
  const columns = [
    {
      name: "#",
      selector: (row, index) => index + 1,
      width: "50px",
    },

    {
      name: "Image",
      selector: (row) =>
        row.profile_img == null ? (
          <img
            width={40}
            style={{ borderRadius: "50%" }}
            className="m-1"
            src="https://cdn.vectorstock.com/i/preview-1x/32/12/default-avatar-profile-icon-vector-39013212.jpg"
            alt="MDN logo"
          />
        ) : (
          <Link to={row.profile_img} target="_blank">
            {" "}
            <img
              width={40}
              style={{ borderRadius: "50%" }}
              className="m-1"
              src={row.profile_img}
              alt="MDN logo"
            />
          </Link>
        ),
      width: "120px",
    },

    {
      name: "Name",
      selector: (row) => (
        <span className="text-capital fs-15">{row.name}</span>
        // {row.first_name + " " + row.last_name}
      ),
      sortable: true,
      width: "200px",
    },


    {
      name: "User Name",
      selector: (row) => (
        <span className="text-capital fs-15">{row.user_name}</span>
      ),
      sortable: true,
      width: "150px",
    },

    {
      name: "Mobile Number",
      selector: (row) => <span>{row.phone}</span>,
      sortable: true,
      width: "200px",
    },

   
    {
      name: "Action",
      selector: (row) => (
        <div>
          {
            <Link to={`/user-view/${row.id}`}>
              <CButton
                className="border-b bg-black m-1"
                data-coreui-toggle="tooltip"
                data-coreui-placement="top"
                title="View"
              >
                <FiEye size={20} />
              </CButton>
            </Link>
          }
          {/* <CButton
            className="border-b bg-black m-1"
            data-coreui-toggle="tooltip"
            data-coreui-placement="top"
            title="Delete"
            onClick={() => {
              if (window.confirm("Are you sure you wish to delete this item?"))
                deleteHandler(row);
            }}
          >
            <AiOutlineDelete size={20} />
          </CButton> */}
        </div>
      ),
      width: "140px",
      sortable: false,
    },
  ];

  // const filteredItems = user.filter(
  //   (item) =>
  //     (item.user_name &&
  //       item.user_name.toLowerCase().includes(filterText.toLowerCase())) ||
  //     (item.first_name &&
  //       item.first_name.toLowerCase().includes(filterText.toLowerCase())) ||
  //     (item.phone &&
  //       item.phone.toLowerCase().includes(filterText.toLowerCase())) ||
  //     (item.last_name &&
  //       item.last_name.toLowerCase().includes(filterText.toLowerCase()))
    
  // );


  const filteredItems = user.filter((item) => {
    // console.log("Filter Text:", filterText);
    const searchWords = filterText.toLowerCase().split(" ");
    // console.log("Search Words:", searchWords);
  
    return searchWords.every((word) => {
      const includes =
        item.user_name.toLowerCase().includes(word) ||
        item.name.toLowerCase().includes(word) ||
        item.phone.toLowerCase().includes(word);
  
      console.log(`${word} found in item: ${includes}`);
      return includes;
    });
  });

  const handleClear = () => {
    if (filterText) {
      setResetPaginationToggle(!resetPaginationToggle);
      setFilterText("");
    }
  };

  const TextField = styled.input`
    height: 32px;
    width: 180px;
    border-radius: 3px;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    border: 1px solid #e5e5e5;
    padding: 0 32px 0 16px;
    &:hover {
      cursor: pointer;
    }
  `;

  //  ----------------------------------------------LOADER TIMER-------------------------------------------------------   

useEffect(() => {
  const timeout = setTimeout(() => {
    setRows(user);
    setPending(false);
  }, 500);
  return () => clearTimeout(timeout);
}, []);
  return (
    <>
      <div className="px-2">
        <CCol xs={12}>
          <CCard className="mb-1">
            <CCardHeader>
            {filterText && (
                <HiArrowSmLeft  style={{width:30, height:30}}
                                onClick={handleClear}/>
                                
                              
                            )}
              <strong>All Users List </strong>
            </CCardHeader>
            <CCardBody>
              <CRow>
                <CCol lg={12}>
                  <CCard color="light" textColor="black" className="mb-3">
                    <CCardHeader>
                      <div className="row ">
                        <CCol sm={3}>
                          <div className="text-center">
                            <TextField
                              type="text"
                              placeholder="Search...."
                              className="mt-1 mb-2"
                              value={filterText}
                              autoFocus
                              onChange={(e) => setFilterText(e.target.value)}
                            />
                          </div>
                        </CCol>
                        
                      </div>
                    </CCardHeader>
                    <CCardBody>
                      {filteredItems.length == 0 ? (
                        <div className="text-center fw-600 my-5 fs-18 text-red">
                          <span>No Users Available</span>
                        </div>
                      ) : (
                        <DataTable
                          columns={columns}
                          data={filteredItems}
                          defaultSortFieldId
                          
                          fixedHeader
                          responsive
                          pagination={10}
                          subHeaderAlign="right"
                          highlightOnHover
                          progressPending={pending}
                          progressComponent={<CustomeLoader />}
                          
                        />
                      )}
                    </CCardBody>
                  </CCard>
                </CCol>
              </CRow>
            </CCardBody>
          </CCard>
        </CCol>
      </div>
    </>
  );
};

export default User;
